<template>
  <div class="navbar-wrapper">
    <div class="navbar" @mouseleave.self="setYear(null)">
      <router-link class="logo-link" to="/"><icon class="logo" name="logo-min"></icon></router-link>
      <div class="navbar__subnav mdl-cell--hide-phone mdl-cell--hide-tablet">
        <div class="navbar__year">
          <router-link to="/" class="mdl-button white-btn mdl-cell--hide-phone mdl-cell--hide-tablet">Geral</router-link>
          <span @mouseenter="setYear(year)" v-for="year in years">
            <router-link :to="'/' + year" class="mdl-button white-btn" :key="year"
            :class="{'mdl-button--raised mdl-button--accent': year === currentYear}">
              {{ year }}
            </router-link>
          </span>
        </div>
        <div class="navbar__month" :style="{visibility: currentYear ? 'visible' : 'hidden'}">
          <router-link class="mdl-button white-btn" v-for="(month, index) in getMonths(currentYear)" :key="month"
          :to="'/' + currentYear + '/' + ('00' + (index + 1)).substr(-2)">{{ month }}</router-link>
        </div>
      </div>
      <select class="navbar__month--mobile" @change="handleMobileNav" :value="$route.path">
        <option v-for="route in routes" :value="route.href">{{ route.name }}</option>
      </select>
    </div>
  </div>
</template>

<script>
import months from './months'
const routes = process.env.ROUTES
const years = routes
  .map(e => e.substr(1))
  .filter(e => e && !e.includes('/'))

const normalizedRoutes = routes.sort().map(e => ({
  href: e,
  name: e === '/' ? 'Rank geral' : e.substr(1)
    .replace(/\/(\d+)/, (all, e) => ' ' + months[Number(e) - 1])
}))

export default {
  data () {
    return {
      years,
      routes: normalizedRoutes,
      currentYear: null
    }
  },
  methods: {
    setYear (year) {
      this.currentYear = year
    },
    getMonths (year) {
      const len = routes.filter(e => e.includes(`/${year}/`)).length
      return months.slice(0, len)
    },
    handleMobileNav (e) {
      this.$router.push(e.target.value)
    }
  }
}
</script>

<style lang="stylus">
.navbar svg
  width: 9em
  height: 6em
</style>

<style lang="stylus" scoped>
.navbar
  max-width: 1280px
  margin: 0 auto
  padding: 0 2em

.navbar-wrapper
  background: rgba(0, 0, 0, 0.1)

.navbar__subnav
  width: 100%
  display: flex
  flex-direction: column
  justify-content: center
  overflow: auto

.logo-link
  display: flex
  align-items: center
  justify-content: center

.navbar__year
  display: flex
  flex-direction: column
  align-items: center

.navbar__month
  display: flex
  overflow: auto
  justify-content: flex-end
  flex-wrap: wrap

.navbar__month--mobile
  width: 100%
  padding: 0.5em 0.25em
  border-radius: 0.25em
  margin: 0.5em 0
  font-family: inherit
  align-self: center

.white-btn
  color: white
  font-size: 1em
  margin: 0.25em
  padding: 0.2em 0.4em

@media screen and (min-width: 480px)
  .navbar
    display: flex
    padding: 0 5em
  .navbar__year
    flex-direction: row
  .navbar__subnav, .navbar__year
    align-items: flex-end
  .navbar__month--mobile
    margin-left: 1em
    
@media screen and (min-width: 840px)
  .navbar__month--mobile
    display: none
</style>



